import React, { Component } from 'react';
import logo from './logo.png';
import { Link } from "react-router-dom";
// import PokemonList from './PokemonList'
// import BerryList from './BerryList'
// import MachineList from './MachineList'

export default class Header extends Component {

    render() {
        return (
            <header style={headerStyle}>
                <img src={logo} className="App-logo" alt="logo" />
                <h1 className="App-title">Welcome to the Pokemon API</h1>

                <Link className='Header-link' to="/pokemon/page/0" onClick={this.props.pokemonClick}>Pokemon</Link>|
                <Link className='Header-link' to='/berries/page/0' onClick={this.props.berryClick}>Berry</Link>|
                <Link className='Header-link' to='/machines/page/0' onClick={this.props.machinesClick}>Machine</Link>
            </header>
        )
    }
}

const headerStyle = {
    background: '#333',
    color: '#fff',
    textAlign: 'center',
    padding: '10px'
}