import React, { Component } from 'react'
import { Redirect } from "react-router-dom";
import axios from 'axios';
import noImage from "./logo.png";

export default class PokemonDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            onePokemonData: null,
            loading: false,
            errorRedirect: false
        };
    }
    async getDatas() {

        try {
            this.setState({ loading: true });
            console.log(this.props);
            let id = Number.parseInt(this.props.match.params.id);
            let res = await axios.get(`https://pokeapi.co/api/v2/pokemon/${id}`);
            this.setState({ onePokemonData: res.data, loading: false, errorRedirect: false });
            console.log(this.state.onePokemonData);
        } catch (e) {
            this.setState({ errorRedirect: true, loading: false });
            console.log(e);
        }

    }

    componentDidMount() {
        this.getDatas();
    }

    renderRedirect = () => {
        if (this.state.errorRedirect) {
            return <Redirect to='/error' />
        }
    }

    checkVaild = () => {
        if (isNaN(this.props.match.params.id)) {
            return <Redirect to='/error' />
        }
    }

    render() {
        let body;
        if (this.state.loading) {
            body = (
                <div>
                    <h1 style={{ textAlign: 'center' }}>POKEMON</h1>
                    <br />
                    <p style={{ textAlign: 'center' }}>Loading...</p>
                </div>
            );
        }
        else {

            let name = this.state.onePokemonData && this.state.onePokemonData.name;
            let img;
            if (this.state.onePokemonData && this.state.onePokemonData.sprites.front_default)
                img = <img alt={name} src={this.state.onePokemonData.sprites.front_default} />
            else
                img = <img alt="Pokemon" src={noImage} width="60" height="60" />
            let abilities = this.state.onePokemonData && this.state.onePokemonData.abilities;
            if (!abilities)
                abilities = [];
            let species = this.state.onePokemonData && this.state.onePokemonData.species;
            if (!species)
                species = "";

            body = (
                <div style={{ textAlign: 'center' }}>
                    {this.checkVaild()}
                    {this.renderRedirect()}
                    <br />
                    <h2> {name} </h2>
                    <br />
                    {img}
                    <br />
                    <b>abilities</b>:
                    <ul className="list-unstyled">
                        {abilities.map(ability => {
                            return <li key={ability.ability.name}>{ability.ability.name}</li>;
                        })}
                    </ul>
                    <br />
                    <p>
                        <b>species</b>:{species.name}
                    </p>
                </div>
            )

        }
        return body;
    }
}
