import React, { Component } from 'react';
import axios from 'axios';
import { Redirect, Link } from "react-router-dom";
// import { LinkContainer } from 'react-router-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css';
import Pagination from 'react-bootstrap/Pagination'

export default class BerryList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            berryData: [],
            prePage: null,
            nextPage: null,
            pageNum: null,
            lastPageNum: null,
            loading: false,
            redirectPage: null,
            navbarClicked: this.props.chick
        };
    }

    async getDatas(pageNum) {
        this.setState({ loading: true });
        try {
            // let pageNum = Number.parseInt(this.props.match.params.page);
            let res = await axios.get(`https://pokeapi.co/api/v2/berry/?offset=${pageNum * 20}&limit=20}`);
            let prePage = res.data.previous;
            let nextPage = res.data.next;
            let numOfAllData = res.data.count;
            let lastPageNum = Number.parseInt(numOfAllData / 20 + 1);
            let data = res.data.results;

            for (const i in data) {
                if (data.hasOwnProperty(i)) {
                    const oneBerry = data[i];
                    let temp = oneBerry.url.split('/');
                    let id = temp[6];
                    data[i].id = id;
                }
            }
            console.log(lastPageNum);
            console.log(res);
            console.log(this.state)
            this.setState({ berryData: data, prePage, nextPage, pageNum, lastPageNum, loading: false });
        } catch (e) {
            console.log(e);
        }
    }

    componentDidMount() {
        this.getDatas(Number.parseInt(this.props.match.params.page));
        // console.log("CLICKED" + this.state.navbarClicked);
    }

    pageChanged = (number) => {
        this.getDatas(number);
        this.setState({ redirectPage: number });
    }

    renderRedirect = () => {
        console.log(this.state.lastPageNum, Number.parseInt(this.props.match.params.page));
        if (this.state.lastPageNum && this.state.lastPageNum <= Number.parseInt(this.props.match.params.page)) {
            console.log("redirect");
            return <Redirect to='/error' />
        }
        if (this.state.redirectPage) {
            return <Redirect to={`/berries/page/${this.state.redirectPage}`} />
        }
    }

    checkVaild = () => {
        console.log(this.props);
        if (isNaN(this.props.match.params.page)) {
            return <Redirect to='/error' />
        }
    }

    render() {
        let body;

        if (this.state.loading) {
            body = (
                <div>
                    <h1 style={{ textAlign: 'center' }}>BERRY</h1>
                    <br />
                    <p style={{ textAlign: 'center' }}>Loading...</p>
                </div>
            );
        }
        else {
            let li = null;
            li = this.state.berryData.map(oneBerry => {
                // console.log(oneBerry.id);
                return (
                    <li key={oneBerry.id}>
                        <Link to={`/berries/${oneBerry.id}`} style={{ color: 'black' }} >{oneBerry.name}</Link>
                    </li>
                );
            })

            // pagination
            let active = this.state.pageNum;
            let items = [];
            if (this.state.prePage) {
                items.push(
                    // <LinkContainer to={`/berries/page/${active - 1}`}>
                    <Pagination.Prev key={'prevButtion'} onClick={this.pageChanged.bind(this, active - 1)} />
                    // </LinkContainer>
                );
            }

            for (let i = 0; i < 5; i++) {
                let number = this.state.pageNum + i;
                items.push(
                    // <LinkContainer to={`/berries/page/${number}`}>
                    <Pagination.Item key={number} alt={`this is page ${number}`} active={number === active} onClick={this.pageChanged.bind(this, number)} >
                        {number + 1}
                    </Pagination.Item>
                    // </LinkContainer>

                );
                if (number + 1 === this.state.lastPageNum)
                    break;
            }

            if (this.state.nextPage) {
                items.push(
                    // <LinkContainer to={`/berries/page/${active + 1}`}>
                    <Pagination.Next key={'nextButtion'} onClick={this.pageChanged.bind(this, active + 1)} />
                    // </LinkContainer>
                );
            }

            body = (
                <div>
                    {this.checkVaild()}
                    {this.renderRedirect()}
                    <ul className="berry-list">{li}</ul>
                    <div style={{ textAlign: 'center', background: '#fff' }}>
                        <Pagination style={{ justifyContent: 'center' }}>{items}</Pagination>
                    </div>
                </div>
            )
        }
        return body
    }
}

