import React, { Component } from 'react';
import axios from 'axios';
import { Redirect, Link } from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.min.css';
import Pagination from 'react-bootstrap/Pagination'

export default class PokemonList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            pokemonData: [],
            prePage: null,
            nextPage: null,
            pageNum: null,
            lastPageNum: null,
            loading: false,
            redirectPage: null
        };
        console.log(this);
    }

    async getDatas(pageNum) {
        this.setState({ loading: true });
        try {
            // let pageNum = Number.parseInt(this.props.match.params.page);
            let res = await axios.get(`https://pokeapi.co/api/v2/pokemon/?offset=${pageNum * 20}&limit=20}`);
            let prePage = res.data.previous;
            let nextPage = res.data.next;
            let numOfAllData = res.data.count;
            let lastPageNum = Number.parseInt(numOfAllData / 20 + 1);
            let data = res.data.results;

            for (const i in data) {
                if (data.hasOwnProperty(i)) {
                    const onePokemon = data[i];
                    let temp = onePokemon.url.split('/');
                    let id = temp[6];
                    data[i].id = id;
                }
            }
            console.log(lastPageNum);
            console.log(res);
            this.setState({ pokemonData: data, prePage, nextPage, pageNum, lastPageNum, loading: false });
        } catch (e) {
            console.log(e);
        }
    }

    componentDidMount() {
        this.getDatas(Number.parseInt(this.props.match.params.page));
    }

    pageChanged = (number) => {
        this.getDatas(number);
        this.setState({ redirectPage: number });
    }

    renderRedirect = () => {
        if (this.state.lastPageNum && this.state.lastPageNum <= Number.parseInt(this.props.match.params.page)) {
            return <Redirect to='/error' />
        }
        if (this.state.redirectPage) {
            console.log("another page");
            return <Redirect to={`/pokemon/page/${this.state.redirectPage}`} />
        }
    }

    checkValid = () => {
        // console.log(this.props);
        if (isNaN(this.props.match.params.page)) {
            // return <Redirect to='/error' />
            return false
        }
        return true
    }

    render() {
        let body;

        if (this.state.loading) {
            body = (
                <div>
                    <h1 style={{ textAlign: 'center' }}>POKEMON</h1>
                    <br />
                    <p style={{ textAlign: 'center' }}>Loading...</p>
                </div>
            );
        }
        else if (!this.checkValid()) {
            body = (<Redirect to='/error' />)
        }
        else {
            let li = null;
            li = this.state.pokemonData.map(onePokemon => {
                return (
                    <li key={onePokemon.id}>
                        <Link to={`/pokemon/${onePokemon.id}`} style={{ color: 'black' }} >{onePokemon.name}</Link>
                    </li>
                );
            })

            // pagination
            let active = this.state.pageNum;
            let items = [];
            if (this.state.prePage)
                items.push(<Pagination.Prev key={'prevButtion'} onClick={this.pageChanged.bind(this, active - 1)} />);

            for (let i = 0; i < 5; i++) {
                let number = this.state.pageNum + i;
                items.push(
                    <Pagination.Item key={number} active={number === active} onClick={this.pageChanged.bind(this, number)}>
                        {number + 1}
                    </Pagination.Item>
                );
                if (number + 1 === this.state.lastPageNum)
                    break;
            }

            if (this.state.nextPage)
                items.push(<Pagination.Next key={'nextButtion'} onClick={this.pageChanged.bind(this, active + 1)} />);

            body = (
                <div>
                    {/* {this.checkValid()} */}
                    {this.renderRedirect()}
                    <ul className="Pokemon-list">{li}</ul>
                    <div style={{ textAlign: 'center', background: '#fff' }}>
                        <Pagination style={{ justifyContent: 'center' }}>{items}</Pagination>
                    </div>
                </div>
            )
        }
        return body
    }
}
