import React, { Component } from 'react';
import './App.css'
import { HashRouter as Router, Route, Switch, Redirect } from "react-router-dom";
import { Helmet } from "react-helmet"
import Header from './components/Header';
// import ContactContainer from './components/ContactContainer';
import PokemonList from './components/PokemonList'
import PokemonDetail from './components/PokemonDetail'
import BerryList from './components/BerryList'
import BerryDetail from './components/BerryDetail'
import MachineList from './components/MachineList'
import MachineDetail from './components/MachineDetail'
import ErrorPage from './components/ErrorPage'

const TITLE = 'Pokemon'

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            pokemonClick: false,
            berryClick: false,
            machinesClick: false
        }
        console.log(this);
    }

    pokemonClickSwitch = () => {
        this.setState({ pokemonClick: !this.state.pokemonClick });
        console.log(this.state.pokemonClick);
    }
    berryClickSwitch = () => {
        this.setState({ berryClick: !this.state.berryClick });
        console.log(this.state.berryClick);
    }
    machineClickSwitch = () => {
        this.setState({ machinesClick: !this.state.machinesClick });
        console.log(this.state.machinesClick);
    }

    render() {
        return (
            <Router>
                <Helmet>
                    <title>{TITLE}</title>
                </Helmet>
                <Header pokemonClick={this.pokemonClickSwitch} berryClick={this.berryClickSwitch} machinesClick={this.machineClickSwitch} />

                <Switch>
                    <Route exact path='/' render={props => (
                        <div className='welcome-page'>
                            <h1>WELCOME PAGE</h1>
                            <p>Pokémon also known as Pocket Monsters in Japan, is a media franchise managed by The Pokémon Company, <br />
                                a Japanese consortium between Nintendo, Game Freak, and Creatures. The franchise copyright is shared <br />
                                by all three companies, but Nintendo is the sole owner of the trademark. The franchise was created by <br />
                                Satoshi Tajiri in 1995, and is centered on fictional creatures called "Pokémon", which humans, known <br />
                                as Pokémon Trainers, catch and train to battle each other for sport. The English slogan for the franchise <br />
                                is "Gotta Catch 'Em All". Works within the franchise are set in the Pokémon universe.</p>
                            <h2>Router</h2>
                            <ul className="list-unstyled">
                                <li key="1">
                                    /pokemon/page/:page
                                </li>
                                <li key="2">
                                    /pokemon/:id
                                </li>
                                <li key="3">
                                    /berries/page/:page
                                </li>
                                <li key="4">
                                    /berries/:id
                                </li>
                                <li key="5">
                                    /machines/page/:page
                                </li>
                                <li key="6">
                                    /machines/page/:page
                                </li>
                            </ul>
                        </div>
                    )} />

                    <Route exact path="/pokemon/page/:page" component={PokemonList} />
                    <Route exact path='/berries/page/:page' component={BerryList} />
                    <Route exact path='/machines/page/:page' component={MachineList} />
                    <Route exact path="/pokemon/:id" component={PokemonDetail} />
                    <Route exact path='/berries/:id' component={BerryDetail} />
                    <Route exact path='/machines/:id' component={MachineDetail} />
                    <Route exact path='/error' component={ErrorPage} />
                    <Redirect from='/*' to='/error' />
                </Switch>

            </Router>
        )
    }
}

export default App;