import React, { Component } from 'react'
import { Redirect } from "react-router-dom";
import axios from 'axios';

export default class BerryDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            oneBerryData: null,
            loading: false,
            errorRedirect: false
        };
    }

    async getDatas() {
        try {
            this.setState({ loading: true });
            console.log(this.props);
            let id = Number.parseInt(this.props.match.params.id);
            let res = await axios.get(`https://pokeapi.co/api/v2/berry/${id}`);
            this.setState({ oneBerryData: res.data, loading: false, errorRedirect: false });
            console.log(this.state.oneBerryData);
        } catch (e) {
            console.log("error!!");
            this.setState({ errorRedirect: true, loading: false });
            console.log(e);
        }
    }

    componentDidMount() {
        this.getDatas();
    }

    renderRedirect = () => {
        if (this.state.errorRedirect) {
            return <Redirect to='/error' />
        }
    }

    checkVaild = () => {
        if (isNaN(this.props.match.params.id)) {
            return <Redirect to='/error' />
        }
    }

    render() {
        let body;
        if (this.state.loading) {
            body = (
                <div>
                    <h1 style={{ textAlign: 'center' }}>BERRY</h1>
                    <br />
                    <p style={{ textAlign: 'center' }}>Loading...</p>
                </div>
            );
        }
        else {
            let name = this.state.oneBerryData && this.state.oneBerryData.name;
            let firmness = this.state.oneBerryData && this.state.oneBerryData.firmness;
            if (!firmness)
                firmness = {};
            let flavors = this.state.oneBerryData && this.state.oneBerryData.flavors;
            if (!flavors)
                flavors = [];
            let max_harvest = this.state.oneBerryData && this.state.oneBerryData.max_harvest;
            let size = this.state.oneBerryData && this.state.oneBerryData.size;
            let smoothness = this.state.oneBerryData && this.state.oneBerryData.smoothness;
            body = (
                <div style={{ textAlign: 'center' }}>
                    {this.checkVaild()}
                    {this.renderRedirect()}
                    <br />
                    <h2> {name} </h2>
                    <p>
                        <b>firmness</b>: {firmness.name}
                        <br />
                        <b>max_harvest</b>: {max_harvest}
                        <br />
                        <b>size</b>: {size}
                        <br />
                        <b>smoothness</b>: {smoothness}
                    </p>
                    <b>flavors</b>:
                    <ul className="list-unstyled">
                        {flavors.map(flavor => {
                            return <li key={flavor.flavor.name}>{flavor.flavor.name}</li>;
                        })}
                    </ul>
                </div>
            );
        }
        return body;
    }
}
