import React, { Component } from 'react'
import { Redirect } from "react-router-dom";
import axios from 'axios';

export default class MachineDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            oneMachineData: null,
            loading: false,
            errorRedirect: false
        };
    }

    async getDatas() {
        try {
            this.setState({ loading: true });
            console.log(this.props);
            let id = Number.parseInt(this.props.match.params.id);
            let res = await axios.get(`https://pokeapi.co/api/v2/machine/${id}`);
            this.setState({ oneMachineData: res.data, loading: false, errorRedirect: false });
            console.log(this.state.oneMachineData);
        } catch (e) {
            this.setState({ errorRedirect: true, loading: false });
            console.log(e);
        }
    }

    componentDidMount() {
        this.getDatas();
    }

    renderRedirect = () => {
        if (this.state.errorRedirect) {
            return <Redirect to='/error' />
        }
    }

    checkVaild = () => {
        if (isNaN(this.props.match.params.id)) {
            return <Redirect to='/error' />
        }
    }

    render() {
        let body;
        if (this.state.loading) {
            body = (
                <div>
                    <h1 style={{ textAlign: 'center' }}>MACHINE</h1>
                    <br />
                    <p style={{ textAlign: 'center' }}>Loading...</p>
                </div>
            );
        }
        else {
            let id = this.state.oneMachineData && this.state.oneMachineData.id;
            let item = this.state.oneMachineData && this.state.oneMachineData.item;
            if (!item)
                item = {};
            let move = this.state.oneMachineData && this.state.oneMachineData.move;
            if (!move)
                move = {};
            let version_group = this.state.oneMachineData && this.state.oneMachineData.version_group;
            if (!version_group)
                version_group = {};
            body = (
                <div style={{ textAlign: 'center' }}>
                    {this.checkVaild()}
                    {this.renderRedirect()}
                    <br />
                    <h2> {id} </h2>
                    <br />
                    <b>item</b>: {item.name}
                    <br />
                    <b>move</b>: {move.name}
                    <br />
                    <b>version_group</b>: {version_group.name}
                </div>
            );
        }
        return body;
    }
}
